import {
  createRouter,
  // createWebHashHistory,
  createWebHashHistory
} from 'vue-router'
// 导入方式一：这种导入方式无法实现懒加载
// import Home from '../views/home/Home'
// 导入方式二：可以实现懒加载机制 （推荐使用）
const Home = () => import('../views/home/Home')
const Commodity = () => import('../views/commodity/Commodity')
const ShopCart = () => import('../views/shopcart/ShopCart')
const PersonalCenter = () => import('../views/personalcenter/PersonalCenter')
const CommodityDetails = () => import('../views/commoditydetails/CommodityDetails')
// 引入底部导航
const FooterTab = () => import('../components/FooterTab')
// 引入立即购买页面
const BuyNow = () => import('../views/buynow/BuyNow')
// 引入编辑地址页
const AddressEdit = () => import('../views/shopcart/AddressEdit')
// 引入地址列表页
const AddressList = () => import('../views/shopcart/AddressList')
// 提交订单页面
const OrderBuy = () => import('../views/orderbuy/OrderBuy')
// 登录页面
const Login = () => import('../views/login/Login')
// 注册页面
const Register = () => import('../views/register/Register')
const routes = [{
    path: '/',
    name: 'DefaultHome',
    // 命名视图
    components: {
      default: Home,
      FooterTab: FooterTab
    }
  }, {
    path: '/home',
    name: 'Home',
    components: {
      default: Home,
      FooterTab: FooterTab
    }
  },
  {
    path: '/commodity',
    name: 'Commodity',
    components: {
      default: Commodity,
      FooterTab: FooterTab
    }
  },
  {
    path: '/commodity/:id',
    name: 'Commodity',
    components: {
      default: Commodity,
      FooterTab: FooterTab
    }
  },
  {
    path: '/shopcart',
    name: 'Shopcart',
    components: {
      default: ShopCart,
      FooterTab: FooterTab
    }
  }, {
    path: '/personalcenter',
    name: 'PersonalCenter',
    components: {
      default: PersonalCenter,
      FooterTab: FooterTab
    }
  }, {
    path: '/commodity_details/:id',
    name: 'CommodityDetails',
    component: CommodityDetails
  }, {
    path: '/buynow',
    name: 'BuyNow',
    component: BuyNow
  }, {
    path: '/setAddress',
    name: 'AddressEdit',
    component: AddressEdit
  },
  {
    path: '/getAddress',
    name: 'AddressList',
    component: AddressList
  },
  {
    path: '/orderbuy',
    name: 'OrderBuy',
    component: OrderBuy
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  }
]

const router = createRouter({
  // history: createWebHashHistory(),
  history: createWebHashHistory(),
  routes
})

export default router
