import {
  createStore
} from 'vuex'
import {
  Toast
} from 'vant'
const setLocalCartList = (state) => {
  const {
    cartList
  } = state
  const cartListString = JSON.stringify(cartList)
  localStorage.cartList = cartListString
}
export default createStore({
  // 数据存放地点
  state: {
    // 当前底部导航下标
    bottomIndex: 0,
    // 轮播图下图片导航区域请求数据id
    itemClassifyID: 0,
    // 商品页请求需要显示的内容
    commodityItem: '精选',
    // 首页头部滚动导航内容存放,商品页面左侧滚动侧边栏需要使用，可不请求接口直接调用
    headerNavData: [],
    // 商品内容
    commodityContent: [],
    // 商品详情内容
    commodityDetails: [],
    // 购物车页面数据存放
    cartData: [],
    // 控制购物车页地址显示与隐藏
    showAddress: false,
    // 购物车页面点击添加进行判断地址列表里面有没有内容，没有跳转添加页面，有就跳转列表页面
    addressList: [],
    // 地址选择列表页当前点击选中的条目索引,方便购物车页面显示地址的地方查找addressList中的第几条数据渲染
    addressIndex: 0,
    // 购物车数据存放
    cartList: {
      // 第一层分类信息
      // "精选":{
      // 第二层商品id对应商品内容
      // id:{
      // id
      // }
      // }
    },
    // 存放不同分类内容，如果为空就删除该分类
    classifyList: [],
    // 商品类别id
    shopId: 0,
    // 总计价格
    totalMoney: 0,
    // 控制全选按钮选中与不选中
    checkTrue: false,
    // 注册的用户信息存放
    user: []
  },
  // 计算属性（用于书写计算方法）
  getters: {

  },
  // 修改状态的方法
  mutations: {
    setBottomIndex: function (state, data) {
      state.bottomIndex = data.index
      // 点击首页的头部滚动导航获取到当前点击的值，修改commodityItem，达到商品页请求显示的信息不同
      state.commodityItem = data.commodityItem
    },
    // 参数一必须传，默认是state
    setItemClassifyID: function (state, index) {
      state.itemClassifyID = index
    },
    // 获取首页头部滚动导航栏的内容，商品页还需要使用，省去商品页再次请求
    getHeaderNav: function (state, data) {
      state.headerNavData = data
    },
    // 获取商品内容信息（作用：在商品页点击侧边栏也能渲染不容内容数据）
    getcommodityContent: function (state, data) {
      state.commodityContent = data
    },
    getCommodityDetails: function (state, data) {
      state.commodityDetails = data
    },
    cartDataList: function (state, data) {
      const {
        shopId,
        productId,
        productInfo
      } = data
      // console.log(shopId + '\t' +
      //   productId + '\t' +
      //   JSON.stringify(productInfo))
      let shopInfo = state.cartList[shopId]
      // console.log(shopInfo)
      if (!shopInfo) {
        shopInfo = {}
      }
      // console.log(state.cartList)
      let product = shopInfo[productId]
      // console.log(product)
      if (!product) {
        product = productInfo
        product.count = 0
        product.check = false
      }
      // if (data.num > 0) {
      //   product.check = true
      // }
      // if (product.count < 0) {
      //   product.count = 0
      // }
      shopInfo[productId] = product
      state.cartList[shopId] = shopInfo
      if (data.num) {
        product.count = product.count + data.num
        // 判断商品数量是不是大于1，如果小于1的话直接删除商品信息
        if (state.cartList[shopId][productId].count <= 0) {
          // product.count = product.count + data.num
          delete state.cartList[shopId][productId]
          // console.log(state.cartList[shopId][productId] === undefined)
          // 如果商品信息没有了的话也一并删除分类信息
          state.classifyList = []
          for (const i in state.cartList[shopId]) {
            state.classifyList.push(state.cartList[shopId][i])
          }
          // 如果该数组的长度如果为空删除该分类内容
          if (state.classifyList.length === 0) {
            delete state.cartList[shopId]
          }
          Toast('商品删除成功')
        }
      } else {
        product.count = product.count + 1
      }
      setLocalCartList(state)
    }
  },
  // ajax异步修改数据
  actions: {},
  // 模块设置
  modules: {}
})
