import {
  createApp
} from 'vue'
import {
  // 侧边栏
  Sidebar,
  SidebarItem,
  // 横向导航
  Tab,
  Tabs,
  // 弹窗吐司
  Toast,
  // 轮播图
  Swipe,
  SwipeItem,
  // 分享面板
  ShareSheet,
  // 动作面板
  ActionSheet,
  // 动作栏点击加入购物车与立即购买
  ActionBar,
  ActionBarIcon,
  ActionBarButton,
  // 地址显示列表
  AddressList,
  // 编辑地址
  AddressEdit,
  // 提交订单
  SubmitBar,
  Checkbox
} from 'vant'
// 如果想需要vant里的样式必须导入这个css文件
import 'vant/lib/index.css'
import App from './App.vue'
import router from './router'
import store from './store'
import 'animate.css'
import './style/index.scss'
import 'element3/lib/theme-chalk/index.css'
import Element3 from 'element3'
import 'iscroll'

createApp(App)
  .use(store)
  .use(router)
  .use(Element3)
  .use(Sidebar)
  .use(SidebarItem)
  .use(Tab)
  .use(Tabs)
  .use(Toast)
  .use(Swipe)
  .use(SwipeItem)
  .use(ShareSheet)
  .use(ActionSheet)
  .use(ActionBar)
  .use(ActionBarIcon)
  .use(ActionBarButton)
  .use(AddressEdit)
  .use(AddressList)
  .use(SubmitBar)
  .use(Checkbox)
  .mount('#app')
